.tabs-slick {
  .tabs-slick-content-box {
    position: relative;
    margin-top: 32px;
    @include media-breakpoint-down(md) {
      margin-top: 24px;
    }
  }

  &.tabs-slick-inited {
    .tabs-slick-content-box {
      .tabs-slick-content:not(.current):not(.tab-slick-fade-in) {
        display: none !important;
      }
    }
  }

  &:not(.tabs-slick-inited) {
    .tabs-slick-content-box {
      .tabs-slick-content:not(:first-of-type) {
        display: none;
      }
    }
    .see-more-box {
      display: none;
    }
  }
  * {
    outline: none !important;
  }
}

.see-more-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.tab-slick-fade-in {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  display: block !important;
  z-index: 99;
  opacity: 0;
}

@import "./layouts/audio-card";
