#banner {
  .banner-with-meeting {
    position: relative;
    .background-box {
      width: 100%;
      img {
        display: block;
        width: 100%;
      }
    }
    .content-box {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: 9;
      .content-wrap {
        width: 624px;
        margin-top: 70px;
        h1 {
          font-size: 42px;
          font-weight: bold;
          line-height: 1.2em;
          margin-bottom: 53px;
        }
        .btn.warm {
          color: #fff;
          background: #ed7b18;
          &:hover {
            background: #ff8419;
          }
        }
      }
    }

    @include media-breakpoint-down(lg) {
      .content-box {
        position: relative;
        .content-wrap {
          margin-top: 0;
          padding: 0 58px;
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          h1 {
            font-size: 32px;
            margin-bottom: 32px;
            text-align: center;
          }
        }
      }
    }
    @include media-breakpoint-down(md) {
      .content-box {
        .content-wrap {
          padding: 0px;
        }
      }
    }
    @include media-breakpoint-down(sm) {
      .content-box {
        .content-wrap {
          h1 {
            font-size: 24px;
            margin-bottom: 24px;
          }
        }
      }
    }
    @include media-breakpoint-down(mc) {
      .content-box {
        .content-wrap {
          h1 {
            font-size: 18px;
            margin-bottom: 18px;
          }
        }
      }
    }
  }
}
