html[dir="rtl"] {
  .article-box {
    .article-list {
      .article-card-meta {
        text-align: right;
      }
    }

    &.layout-3 {
      .article-card-body {
        padding-right: 0;
        padding-left: 24px;
        @include media-breakpoint-down(sm) {
          padding-left: 16px;
        }
      }
    }

    &.layout-4,
    &.layout-5 {
      .article-list {
        .article-card-body {
          padding-left: 0;
          padding-right: 48px;

          .article-card-meta {
            &::before {
              left: 0;
              right: -50px;
            }
          }

          @include media-breakpoint-down(sm) {
            padding-right: 42px;
            .article-card-meta {
              &::before {
                right: -42px;
              }
            }
          }
          @include media-breakpoint-down(mc) {
            padding-right: 0;
          }
        }
      }
    }

    &.layout-6 {
      .layout-6-tablet .right .article-card .article-card-cover {
        margin-right: 0;
        margin-left: 20px;
      }
      .article-list .article-card:not(:first-of-type) .article-card-cover {
        margin-left: 16px;
        margin-right: 0;
      }
      .tablet-title{
        i{
          transform-origin: center;
          transform: translateY(-10px) rotateZ(180deg);
        }
      }
    }

    &.layout-7{
      @media (min-width: 310px) and (max-width: 767.78px) {
        .article-card-cover{
          margin-right: 0;
          margin-left: 16px;
        }
      }
    }

    &.layout-8{
      @include media-breakpoint-up(md){
        .article-card-body{
          margin-left: 0;
          margin-right: 24px;
        }
      }
    }
  }

  .article-card-section-title{
    i{
      right: unset;
      left: 0;
      transform: translateY(4px) rotateZ(180deg);
    }
    @include media-breakpoint-down(md){
      padding-right: 0;
      padding-left: 24px;
    }
  }
}
