.article-box.layout-4.layout-9 {
  .article-list {
    .article-card-body {
      .article-card-title {
        margin-bottom: 32px;
      }
    }

    .article-card{
      &:nth-of-type(4){
        .article-card-body{
          border-bottom: 1px solid rgba(0, 0, 0, 0.08);
        }
      }
      &:nth-of-type(5),
      &:nth-of-type(6){
        margin-bottom: 0;
        .article-card-body{
          .article-card-title{
            margin-bottom: 0;
          }
        }
      }
    }

    @include media-breakpoint-down(md){
      .article-card{
        .article-card-body {
          .article-card-title {
            margin-bottom: 24px;
          }
        }
        &:nth-of-type(4){
          display: block;
          margin-bottom: 0;
          .article-card-body{
            .article-card-title{
              margin-bottom: 0;
              height: auto;
            }
          }
          .article-card-body{
            border-bottom: none;
          }
        }
        &:nth-of-type(5),
        &:nth-of-type(6){
          display: none;
        }
      }
    }
    @include media-breakpoint-down(sm){
      
    }
  }
}
